import React from 'react';
import styles from './ProjectModal.module.css';
import {Swiper, SwiperSlide} from "swiper/react";
import {Pagination, Navigation} from "swiper/modules";
import 'swiper/css';
import 'swiper/css/navigation'; //   
import 'swiper/css/pagination'; //   

function ProjectModal({ project, onClick }) {

  return (
    <modal className={styles.modal}>
      <div className={styles.head}>
        <h2>{project.name}</h2>
        <button className={styles.closeButton} onClick={onClick} aria-label="">
          
        </button>
      </div>

      <Swiper
        modules={[Navigation, Pagination]} //   
        spaceBetween={20}                  //    ()
        slidesPerView={1}                  //  1  (    1)
        navigation={true}                  //    "/"
        pagination={{                      //   
          clickable: true,                 //  
          dynamicBullets: true             //   ()
        }}
        loop={false}                       //    
      >
        {project.images.map((img, i) => (
          <SwiperSlide key={i}>
            <img className={styles.image} src={img} alt={`slide ${i}`} />
          </SwiperSlide>
        ))}
      </Swiper>

      <main>
        <p>{project.fullDescription.description}</p>
        <p></p>
        <ul>
          {project.fullDescription.technologies.map((techno, i) => (<li key={i}>{techno}</li>))}
        </ul>

        <p></p>
        <ul>
          {project.fullDescription.keyFeatures.map((feature, i) => (<li key={i}>{feature}</li>))}
        </ul>
      </main>
    </modal>
  );
}

export default ProjectModal;
